<template>
  <div>
    <el-button type="success"
               style="margin-bottom: 20px;"
               @click="getMapDataList">
      刷新
    </el-button>
    <el-input
        v-model="pageParams.keyword"
        placeholder="模糊搜索"
        @input="getMapDataList"
        clearable prefix-icon="Search"
        style="margin-left: 20px; width: 400px; margin-bottom: 20px;"
    />
    <el-button
        type="primary"
        @click="addMapData = true"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      新增IP数据
    </el-button>
    <el-button
        type="primary"
        @click="updateOciCfgIpData"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      更新已有的oci配置的IP信息
    </el-button>
    <el-button
        type="danger"
        @click="batchRemove"
        :disabled="!batchSelected.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量删除
    </el-button>
    <!-- 可滑动的表格 -->
    <div style="overflow-x: auto;">
      <el-table
          :data="mapDataPageRsp"
          style="width: 100%; min-width: 800px;"
          v-loading="loading"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="35"/>
        <el-table-column label="IP地址" width="300">
          <template #default="{ row }">
            <el-tooltip :content="row.ip" placement="top">
              <el-tag type="success" @click="copyToClipboard(row.ip)">
                {{ row.ip }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="国家">
          <template #default="{ row }">
            <el-tooltip :content="row.country" placement="top">
              <el-tag type="primary">
                {{ row.country }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="地区">
          <template #default="{ row }">
            <el-tooltip :content="row.area" placement="top">
              {{ row.area }}
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="城市">
          <template #default="{ row }">
            <el-tooltip :content="row.city" placement="top">
              {{ row.city }}
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="组织">
          <template #default="{ row }">
            <el-tooltip :content="row.org" placement="top">
              {{ row.org }}
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="ASN">
          <template #default="{ row }">
            <el-tooltip :content="row.asn" placement="top">
              <el-tag type="warning">
                {{ row.asn }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="纬度">
          <template #default="{ row }">
            <el-tooltip :content="row.lat" placement="top">
              <el-tag type="primary">
                {{ row.lat }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="经度">
          <template #default="{ row }">
            <el-tooltip :content="row.lng" placement="top">
              <el-tag type="primary">
                {{ row.lng }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="创建时间">
          <template #default="{ row }">
            <el-tooltip :content="row.createTime" placement="top">
              <el-tag type="info">
                {{ row.createTime }}
              </el-tag>
            </el-tooltip>
          </template>
        </el-table-column>

        <el-table-column label="操作" width="220">
          <template #default="{ row }">
            <el-button @click="updateIpInfo(row)" type="success" link>
              更新IP信息
            </el-button>
            <el-button @click="handleRemove(row)" type="danger" link>
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="pageParams.currentPage"
        v-model:page-size="pageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>

</template>

<script>
import {confirmDialog, copyToClipboard} from "@/js/common";

export default {
  components: {},
  mounted() {
    this.getMapDataList()
  },
  data() {
    return {
      addMapData: true,
      loading: true,
      pageParams: {
        keyword: '',
        currentPage: 1,
        pageSize: 10,
      },
      mapDataPageRsp: [
        {
          id: '1',
          ip: '1.1.1.1',
          country: 'AU',
          area: 'New South Wales',
          city: 'Sydney',
          org: 'CLOUDFLARENET',
          asn: 'AS13335',
          lat: -33.859336,
          lng: 151.203624,
          createTime: '2025-07-30 15:08:47'
        }
      ],
      total: 0,
      batchSelected: [],
      removeIds: [],
    };
  },
  methods: {
    copyToClipboard,
    async getMapDataList() {
      this.loading = false
    },
   async updateOciCfgIpData() {
     const confirmed = await confirmDialog('确定要更新所有已存在的oci配置的IP数据吗？', '更新已有的oci配置的IP信息');
     if (!confirmed) {
       this.$message.info('已取消');
       return;
     }

   },
    async batchRemove() {
      const confirmed = await confirmDialog('确定要删除IP数据吗？', '删除首页地图IP数据');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.loading = true
      this.removeIds = this.batchSelected.map(record => record.id);

      // todo

      this.cleanCache()
    },
    async updateIpInfo(row) {
      const confirmed = await confirmDialog('确定要更新IP：' + row.ip + ' 的数据信息吗？', '更新IP数据');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.loading = true

      console.log(row)
      this.cleanCache()
    },
    cleanCache() {
      this.batchSelected = []
      this.loading = false
    },
    handleRemove(row) {
      this.batchSelected.push(row)
      this.batchRemove()
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.getCfCfgList();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.getCfCfgList();
    },
    handleSelectionChange(val) {
      this.batchSelected = val;
    },
  }
}
</script>

<style scoped>

</style>